	-webkit-animation: anim-up 2.5s cubic-bezier(.785,.135,.15,.86);
	animation: anim-up 2.5s cubic-bezier(.785,.135,.15,.86);
}
@-webkit-keyframes anim-down{
	0%{
		bottom:0;
		height:0;
	}
	45%{
		bottom:0;
		height:100%;
	}
	55%{
		bottom:0;
		height:100%;
	}
	100%{
		height:0%;
		top:0;
	}
}
